import axios from 'axios';
import React, {useState} from 'react'
import {Alert, Button, Form, Modal, Spinner, Stack} from 'react-bootstrap';

function Promote() {
  const [confirmModalShow, setConfirmModalShow] = useState(false);
  const [workState, setWorkState] = useState(-1);
  const [challengeN, setChallengeN] = useState(-1);
  const [userChallenge, setUserChallenge] = useState(0);
  const [working, setWorking] = useState(false);

  function startPromote() {
    setConfirmModalShow(true);
    setWorkState(-1);
    setUserChallenge(0);
    setChallengeN(Math.round(Math.random() * 100) + 10)
  }

  function closeConfirmModal() {
    setConfirmModalShow(false);
  }

  function beginWork() {
    if (challengeN * (challengeN + 1) !== userChallenge * 2) {
      setWorkState(1);
      return;
    }
    setWorking(true);
    axios.patch('/manage/api/bulk/promote')
      .then(res => {
        setWorkState(2);
      })
      .catch(err => {
        setWorkState(3);
      })
      .finally(() => {
        setWorking(false);
      });
  }

  return (
    <>
      <Stack gap={1}>
        <h2></h2>
        <p>  </p>
        <ul>
          <li> 1, 2   1 .</li>
          <li> 1, 2     .</li>
          <li> 3  IonID .</li>
        </ul>
        <Button variant='outline-primary' className={'align-self-baseline'} onClick={startPromote}> </Button>
      </Stack>

      <Modal show={confirmModalShow} dialogClassName='modal-dialog-centered'>
        <Modal.Header>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {!working &&
            <>
              {(workState === -1 || workState === 1) &&
                <Stack gap={1}>
                  <p>  .  </p>
                  <ul>
                    <li> 1, 2   1 .</li>
                    <li> 1, 2     .</li>
                    <li> 3  IonID .</li>
                  </ul>
                  <p>   1  N  {challengeN}   N  .</p>
                  <Form.Control
                    type='number'
                    value={userChallenge}
                    placeholder=''
                    onChange={e => setUserChallenge(Number.parseInt(e.target.value))}
                  />
                  {workState === 1 &&
                    <p className='m-1 text-danger'> .</p>
                  }
                </Stack>
              }
              {workState === 2 &&
                <Alert variant={'success'}>  .</Alert>
              }
              {workState === 3 &&
                <Alert variant={'danger'}>
                  <p>   .</p>
                  <p>  .</p>
                </Alert>
              }
            </>
          }
          {working &&
            <Stack className={'align-items-center'}>
              <Spinner animation='grow'/>
              <p className='my-3'>.</p>
            </Stack>
          }
        </Modal.Body>
        {!working &&
          <Modal.Footer>
            {(workState === -1 || workState === 1) &&
              <>
                <Button onClick={beginWork}></Button>
                <Button onClick={closeConfirmModal}></Button>
              </>
            }
            {(workState === 2 || workState === 3) &&
              <>
                <Button onClick={closeConfirmModal}></Button>
              </>
            }
          </Modal.Footer>
        }
      </Modal>
    </>
  );
}

export default Promote;
