import axios from "axios";
import React, {useEffect, useState} from "react";
import {Alert, Button, ButtonGroup, Stack, Table} from "react-bootstrap";

type NsReqProps = {
  id: number,
  name: string,
  time: number,
  scode: number,
  place: string,
  super: string,
  reason: string,
  status: string,
  accept: (id: number) => void,
  deny: (id: number) => void

}

function NsReq(props: NsReqProps) {
  const id = props.id;

  const accept = props.accept;
  const deny = props.deny;

  let cc, sh;
  if (props.status === 'APPROVED') {
    cc = 'table-success text-success text-center';
    sh = 'APPROVED';
  }
  if (props.status === 'DENIED') {
    cc = 'table-danger text-danger text-center';
    sh = 'DENIED';
  }
  if (props.status === 'REQUESTED') {
    cc = 'text-center';
    sh = 'REQUESTED';
  }
  if (props.status === 'NO_SUPERVISOR') {
    cc = 'table-warning text-danger text-center';
    sh = 'NO SUPERVISOR';
  }

  let nsTime = '  ';
  switch (props.time) {
    case 0:
      nsTime = '8';
      break;
    case 1:
      nsTime = '1';
      break;
    case 2:
      nsTime = '2';
      break;
    case 3:
      nsTime = ' 1';
      break;
    case 4:
      nsTime = ' 2';
      break;
    case 5:
      nsTime = ' 1';
      break;
    case 6:
      nsTime = ' 2';
  }

  return (
    <tr>
      <td>{props.name}</td>
      <td>{props.scode}</td>
      <td>{nsTime}</td>
      <td>{props.place}</td>
      <td>{props.super}</td>
      <td>{props.reason}</td>
      <td className={cc}>{sh}</td>
      <td>
        <ButtonGroup>
          <Button variant='outline-success' onClick={() => accept(id)}></Button>
          <Button variant='outline-danger' onClick={() => deny(id)}></Button>
        </ButtonGroup>
      </td>
    </tr>
  );
}

function AcceptNs() {
  const [ws, setWs] = useState<number>(0);
  const [nsLst, setNsLst] = useState<any[]>([]);
  const [date, setDate] = useState<string>('');

  useEffect(() => {
    loadNs();
  }, []);

  function loadNs() {
    axios.get('/manage/api/ns/get')
      .then(res => {
        const data = res.data['result'];
        setDate(data['date']);
        setNsLst(data['nss']);
      })
      .catch(err => {
        switch (err.response?.data['result']) {
          case 1:
            setWs(1);
            break;
          default:
            setWs(-1);
            break;
        }
      })
  }

  const rLst: any[] = [];
  if (ws === -1) {
    rLst.push(
      <tr className='table-danger'>
        <td colSpan={8}> .</td>
      </tr>
    );
  } else if (ws === 1) {
    rLst.push(
      <tr className='table-danger'>
        <td colSpan={8}> .</td>
      </tr>
    );
  } else if (nsLst.length === 0) {
    rLst.push(
      <tr>
        <td colSpan={8}>   .</td>
      </tr>
    );
  } else {
    nsLst.forEach(e => {
      if (e['v']) {
        rLst.push(
          <NsReq
            id={e['id']}
            name={e['name']}
            time={e['time']}
            scode={e['rscode']}
            place={e['place']}
            super={e['super']}
            reason={e['reason']}
            status={e['status']}
            accept={(id) => ctrl(id, true)}
            deny={(id) => ctrl(id, false)}
          />
        );
      } else {
        rLst.push(
          <tr className='table-warning'>
            <td colSpan={8}>  .</td>
          </tr>
        )
      }
    });
  }

  function ctrl(id: number, accept: boolean) {
    axios.patch('/manage/api/ns/accept', {
      id: id,
      ac: accept
    })
      .then(res => {
        loadNs();
      })
      .catch(err => {
        switch (err.response?.data['result']) {
          case 1:
            setWs(1);
            break;
          case 2:
            setWs(2);
            break;
          case 3:
            setWs(3);
            break;
          default:
            setWs(-1);
            break;
        }
      });
  }

  return (
    <Stack gap={1}>
      <h2>  </h2>
      <p>     .</p>
      <div className='table-cover'>
        <Table>
          <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          </thead>
          <tbody>{rLst}</tbody>
        </Table>
      </div>
      <Stack direction={'horizontal'} gap={2}>
        <Button variant='outline-primary' onClick={loadNs}></Button>
        <p className='my-2'>{date}</p>
      </Stack>
      {ws === 2 &&
        <Alert variant='danger'> .</Alert>
      }
      {ws === 3 &&
        <Alert variant='danger w-fit'>   .</Alert>
      }
    </Stack>
  )
}

export default AcceptNs;
