import React, {useState} from "react";
import {Alert, Button, ButtonGroup, Stack} from "react-bootstrap";
import axios from "axios";


type ChangeNsPresetProps = {
  timePreset: number,
  updatePreset: (mode: number) => void
}

function ChangeNsPreset(props: ChangeNsPresetProps) {
  const [working, setWorking] = useState<boolean>(false);
  const [workState, setWorkState] = useState<number>(0);

  function exec(mode: number) {
    setWorking(true);
    axios.patch('/manage/api/ns/mode/set', {
      mode: mode
    })
      .then(() => {
        props.updatePreset(mode);
      })
      .catch(err => {
        switch (err.response?.data['result']) {
          case 1:
            setWorkState(1);
            break;
          case 2:
            setWorkState(2);
            break;
          case 3:
            setWorkState(3);
            break;
          default:
            setWorkState(-1);
            break;
        }
      })
      .finally(() => {
        setWorking(false);
      });
  }

  return (
    <Stack className={'align-items-start'} gap={1}>
      <h2>   </h2>
      <ButtonGroup>
        <Button disabled={working} onClick={() => exec(0)}
                className='w-fit'>3 </Button>
        <Button disabled={working} onClick={() => exec(1)}
                className='w-fit'>4 </Button>
      </ButtonGroup>

      <div>
        <p className={'my-2'}>    : {props.timePreset === 0 ? "3" : "4"}</p>
        {props.timePreset === 0 &&
          <ul>
            <li>8</li>
            <li>1</li>
            <li>2</li>
          </ul>
        }
        {props.timePreset === 1 &&
          <ul>
            <li> 1</li>
            <li> 2</li>
            <li> 1</li>
            <li> 2</li>
          </ul>
        }
      </div>
      {workState === 1 &&
        <Alert variant='danger w-100'> .</Alert>
      }
      {workState === 2 &&
        <Alert variant='danger w-100'> .</Alert>
      }
      {workState === 3 &&
        <Alert variant='danger w-100'>      .</Alert>
      }
      {workState === -1 &&
        <Alert variant='danger w-100'>    .</Alert>
      }
    </Stack>
  );
}

export default ChangeNsPreset;
