import {Stack, Table} from "react-bootstrap";
import {useEffect, useState} from "react";
import axios from "axios";

type userType = {
  id: number,
  name: string,
  grade: number,
  clas: number,
  scode: number,
  joinDate: string,
  lastLogin: string
}

function GeneralSettings() {
  const [userInfo, setUserInfo] = useState<userType | undefined>();
  const [loadUserInfoState, setLoadUserInfoState] = useState<number>(0);

  useEffect(() => {
    axios.get('/user/api/profile/get')
      .then(res => {
        setUserInfo(res.data.result);
      })
      .catch(err => {
        switch (err.response?.data['result']) {
          case 1:
            setLoadUserInfoState(1);
            break;
          case 2:
            setLoadUserInfoState(2);
            break;
          default:
            setLoadUserInfoState(3);
            break;
        }
      });
  }, []);

  return (
    <Stack>
      <h2></h2>
      <Table>
        <tbody>
        <tr>
          <td></td>
          <td>{userInfo?.name}</td>
        </tr>
        <tr>
          <td></td>
          <td>{userInfo?.grade}{userInfo?.clas}{userInfo?.scode}</td>
        </tr>
        <tr>
          <td></td>
          <td>{userInfo?.id}</td>
        </tr>
        <tr>
          <td> </td>
          <td>{userInfo?.lastLogin} KST</td>
        </tr>
        <tr>
          <td></td>
          <td>{userInfo?.joinDate} KST</td>
        </tr>
        </tbody>
      </Table>
      {loadUserInfoState === 1 && <p> .</p>}
      {loadUserInfoState === 2 && <p>   .</p>}
      {loadUserInfoState === 3 && <p>   .</p>}
    </Stack>
  );
}

export default GeneralSettings;
