import axios from "axios";
import React, {useState} from "react";
import {Alert, Button, Form, InputGroup, Stack, Table} from "react-bootstrap";
import {inRange} from "../../../service/checker";
import {changeBit, getBit} from "../../../service/bitmask";

function AcceptPwdChange() {
  const [usrLoaded, setUsrLoaded] = useState<boolean>(false);
  const [id, setId] = useState<string>('');
  const [workState, setWorkState] = useState<number>(-1);
  const [workStaea, setWorkStaea] = useState<number>(-1);
  const [reqData, setReqData] = useState<{
    ionid: string,
    name: string,
    scode: number,
    status: string
  }>({ionid: '', name: '', scode: 0, status: ''});
  const [reqUid, setReqUid] = useState<number>(0);
  const [urlToken, setUrlToken] = useState<string>('');
  const [formState, setFormState] = useState<number>(0);

  function load() {
    let state = 0;
    if (!inRange(4, 30, id.length)) state = changeBit(state, 0);
    setFormState(state);
    if (state !== 0) return;

    axios.get('/manage/api/reset-passwd/query', {
      params: {id: id}
    })
      .then(res => {
        setReqData({
          ionid: res.data['result']['id'],
          name: res.data['result']['name'],
          scode: res.data['result']['scode'],
          status: res.data['result']['status']
        });
        setUsrLoaded(true);
        setReqUid(res.data['result']['uid']);
        setWorkState(0);
      })
      .catch(err => {
        switch (err?.response.data['result']) {
          case 0:
            setWorkStaea(4);
            break;
          case 2:
            setWorkState(1);
            break;
          case 3:
            setWorkState(2);
            break;
          default:
            setWorkState(5);
        }
      });
  }

  function ctrl(state: boolean) {
    let fstate = 0;
    if (!inRange(4, 30, id.length)) fstate = changeBit(fstate, 0);
    setFormState(fstate);
    if (fstate !== 0) return;

    axios.patch('/manage/api/reset-passwd/accept', {
      accept: state,
      reqUid: reqUid
    })
      .then(res => {
        if (state) {
          setUrlToken(res.data['result']);
          setWorkStaea(0);
        } else setWorkStaea(5);
      })
      .catch(err => {
        switch (err?.response.data['result']) {
          case 1:
            setWorkStaea(1);
            break;
          case 3:
            setWorkStaea(2);
            break;
          case 4:
            setWorkStaea(3);
            break;
          case 2:
          default:
            setWorkStaea(4);
        }
      })
      .finally(() => {
        load();
      });
  }

  let status = '';
  switch (reqData.status) {
    case 'REQUESTED':
      status = 'REQUESTED (code not saw)';
      break;
    case 'WAITING':
      status = 'REQUESTED (code saw)';
      break;
    default:
      status = reqData.status;
  }

  function copyLink() {
    navigator.clipboard.writeText('https://ionya.cc/auth/iforgot/reset?token=' + urlToken);
  }

  return (
    <Stack>
      <h2>  </h2>
      <InputGroup className={'input-mx-lg'}>
        <Form.Control type={'text'}
                      placeholder={'IonID'} aria-label={'IonID'}
                      value={id}
                      isInvalid={getBit(formState, 0) === 1}
                      onChange={e => setId(e.target.value)}
        />
        <Button onClick={load}></Button>
        <Button onClick={() => ctrl(true)} disabled={!usrLoaded}></Button>
        <Button onClick={() => ctrl(false)} disabled={!usrLoaded}></Button>
      </InputGroup>
      <div className={'table-cover'}>
        <Table>
          <thead>
          <tr>
            <th>IonID</th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          </thead>
          <tbody>
          {workState !== 0 &&
            <tr>
              <td colSpan={4}>No data</td>
            </tr>
          }
          {workState === 0 &&
            <tr>
              <td>{reqData.ionid}</td>
              <td>{reqData.name}</td>
              <td>{reqData.scode}</td>
              <td>{status}</td>
            </tr>
          }
          </tbody>
        </Table>
      </div>
      {workState === 1 && <Alert variant={'danger'}> IonID    .</Alert>}
      {workState === 2 && <Alert variant={'danger'}>  IonID.</Alert>}
      {workState === 4 && <Alert variant={'danger'}> .</Alert>}
      {workState === 5 && <Alert variant={'danger'}>  .</Alert>}

      {workStaea === 0 &&
        <Alert variant={'success'}>
          <p className={'my-1'}>   .</p>
          <p className={'my-1'}>      .        .</p>
          <Button variant={'outline-success'} onClick={copyLink}>   </Button>
        </Alert>
      }
      {workStaea === 5 && <Alert variant={'success'}>   .</Alert>}
      {workStaea === 1 && <Alert variant={'danger'}> .</Alert>}
      {workStaea === 2 && <Alert variant={'danger'}>   .</Alert>}
      {workStaea === 3 && <Alert variant={'danger'}>  REQUESTED  .</Alert>}
      {workStaea === 4 && <Alert variant={'danger'}>  .</Alert>}
    </Stack>
  );
}

export default AcceptPwdChange;
